@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  box-sizing: border-box;
}

body{
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lista{
  margin-top: -150px;
}

.loading{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width:760px){
  .lista{
    margin-top: 0px;
  }
}
.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0 0 0 30px;
}

.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: 0.5s ease-in-out;
    -webkit-filter: opacity(0.9);
            filter: opacity(0.9);

}

.movieRow--item img:hover{
    width: 100%;
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    transition: 0.3s ease-in-out;
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

.movieRow--list{
    transition: all ease .5s;
}

.movieRow--left, .movieRow--right{
    position:absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    font-size: 100px;
    cursor: pointer;
    opacity: 0;
    z-index: 999;
    transition: all ease 0.8s;
}

.movieRow--left{
    left:0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movieRow--right{
    right:0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right{
    opacity: 1;
}


@media (max-width:760px){
    .movieRow--left,.movieRow--right{
        opacity: 1;
    }    
}
.featured{
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.featured--vertical{
    height: inherit;
    width: inherit;
    background-size: cover;
    background-position: center;
    background-image: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    height: inherit;
    width: inherit;
    background-size: cover;
    background-position: center;
    background-image: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 50px;
    padding-left: 30px;
    padding-bottom: 150px;
}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top:15px;
}

.featured--points, 
.featured--year, 
.featured--seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color:#999;
    max-width: 40%;
    box-sizing: border-box;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
}

.featured--buttons{
    margin-top: 15px;
}

.featured--whatchButton,
.featured--listButton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    cursor: pointer;
    transition: all ease 0.2s;
}

.featured--whatchButton{
    color: #111;
    background-color: #fff;
}

.featured--whatchButton{
    color: #111;
    background-color: #fff;
}

.featured--listButton{
    color: #fff;
    background-color: #333;
}

.featured--whatchButton:hover, 
.featured--listButton:hover{
    opacity: 0.7;
}

.featured--genres{
    color: #999;
    margin-top: 15px;
    font-size: 18px;
}

@media (max-width:760px){
   .featured{
       height: 90vh;
   }
   .featured--name{
       font-size: 40px;
   }
   .featured--info{
        font-size: 16px;
   }
   .featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 100%;
    box-sizing: border-box;
}
   .featured--whatchButton, .featured--listButton{
       font-size: 16px;
   }
   .featured--genres{
       font-size: 14px;
   }
   .featured--horizontal {
    height: inherit;
    width: inherit;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 0px;
    margin: auto;
}
}

header{
    position: fixed;
    z-index: 9999;
    top:0;
    right: 0;
    left: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background-color: transparent;
    transition: all ease 0.5s;
}

header.black{
    background-color: #141414;
}

.header--logo{
    height: 25px;
}

.header--logo img{
    height: 100%;
}

.header--user{
    height: 35px;
}

.header--user img{
    height: 100%;
    margin: auto;
    border-radius: 3px;
}
footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 50px 0;
}

footer p{
    margin: 3px auto;
}
